.circular-chart {
  display: block;
  margin: 10px auto;
  max-width: 80%;
  max-height: 250px;
}
.alltop {
  width: 80vw;
  height: 40vh;
  display: flex;
}
.topgenre {
  height: 100%;
  width: 25vw;
  position: relative;

  text-align: center;
}
.genrename {
  font-family: sans-serif;
  font-size: 1.5em;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: rgb(255, 255, 255);
}

.circle {
  stroke: #00ff8c;

  fill: #161616;
  stroke-width: 2.8;
  stroke-linecap: round;
  animation: progress 1s ease-out forwards;
}

@keyframes progress {
  0% {
    stroke-dasharray: 0 100;
  }
}
